/* 整体 */
.App-BotResponse {
  position: absolute;
  height: 100vh;
  width: 100vw;
  left: 0;
  top: 0;
}

/* 回复文字 */
.App-BotResponse-Activity {
  background-color: transparent;
  border-radius: 2vmin;
  color: black;
  font-size: 4vmin;
  margin: 5vmin 0 0 0;
  padding: 2vmin 3vmin 2vmin;
}

/* 附件列表 */
.App-BotResponse-Attachments {
  height: 100vh;
  width: 100vw;
}

/* 附件卡片（普通卡片效果） */
.App-BotResponse-Attachments .App-BotResponse-Attachment {
  background-color: white;
  border-radius: 1vmin;
  margin: 2vmin 0 2vmin 2vmin;
  width: auto;
  max-width: 72vw;
}

.App-BotResponse-Attachments li:last-child {
  padding-right: 2vmin;
}


/* 附件卡片（毛玻璃效果） */
.App-BotResponse-Attachments .Glassmorphic-Attachment {

  /* 基础 */
  /* position: absolute; */
  display: flex;

  /* 形状 */
  width: auto;
  /* Adding our borders*/
  border-radius: 1vmin;
  border: 0px solid rgba( 255, 255, 255, 0.18 );
  z-index: 0;

  /* 颜色质感透明度 */
  background: rgba( 255, 255, 255, 0.8 );
  backdrop-filter: blur( 7.5px );
  -webkit-backdrop-filter: blur( 7.5px );
  /*Adding the shadow*/ 
  box-shadow: 0 1px 4px 0 rgba( 0, 0, 0, 0.18 );

  /* 内容 */
  justify-content: space-evenly;

  /* 位置 */
  left:0; 
  right:0;
  margin:auto;
  margin: 2vmin 2vmin 2vmin 2vmin;
}

.App-BotResponse-Attachments .Glassmorphic-Attachment .Multi {
  height: 50vh;
}

.App-BotResponse-Attachments .Glassmorphic-Attachment .Single {
  height: 80vh;
}