/******************************************************************
  毛玻璃效果
*******************************************************************/
.App-SelectButton-Glass-Morphic {
  /* 基本 */
  /* display: flex; */
  /* overflow: overlay; */
  position: absolute;
  
  /* 形状 */
  width: 16vmin;
  height: 16vmin;
  z-index: 3;
  /* Adding our borders*/
  border-radius: 16px;
  border: solid 1px rgba( 255, 255, 255, 0.18 );

  /* 颜色质感透明度 */
  background: ghostwhite;
  backdrop-filter: blur( 10px );
  -webkit-backdrop-filter: blur( 10px );
  /*Adding the shadow*/
  /* box-shadow: 0 8px 32px 0 rgba( 0, 0, 0, 0.18 ); */

  /* 内容 */
  justify-content: center;
  color: #0073FF;
}

.chat {
/* 位置 */
  left:25%; 
  right:auto;
  margin:auto;
  bottom: 50%;
}

.holo {
/* 位置 */
  left:auto; 
  right:25%;
  margin:auto;
  bottom: 50%;
}

.display {
/* 位置 */
  left:auto; 
  right:30%;
  margin:auto;
  bottom: 30%;
}

.minimizable {
/* 位置 */
  left:30%; 
  right:auto;
  margin:auto;
  bottom: 30%;
}


.App-SelectButton-Glass-Morphic:active,
.App-SelectButton-Glass-Morphic:hover {
  opacity: .8;
}

.App-SelectButton > svg {
  fill: White;
}

.App-SelectButton-Glass-Morphic.dictating {
  background-color: #EF0000;
  border-color: #EF0000;
  color: rgb(119, 119, 119);
  opacity: .8;
}

.App-SelectButton-Glass-Morphic.dictating > svg {
  fill: White;
}